body {
  margin: 0;
  overflow: hidden;
  color: white;
  text-align: center;
}
#logo {
  width: 100px;
  height: 100px;
  border-radius: 100px;
  top: 0px;
  position: relative;
}
h1 {
  position: absolute;
  width: 100%;
  z-index: 1;
  font-size: 1.5rem;
}

a {
  color: white;
}
a:hover {
  color: purple;
}

#scene-container {
  position: absolute;
  width: 100%;
  height: 100%;
}
#details {
  position: absolute;
  bottom: 0;
}
#devdetails {
  position: absolute;
  bottom: 10;
  top: 10;
  font-size: 25px;
  padding: 20;
  right: 0;
}
